import {
  FETCH_CITIES_LIST,
  FETCH_RIGHT_COORDINATES,
  FETCH_LEFT_COORDINATES,
} from "./types";
const lefCoordinate =
  localStorage.weather_app_LEFT_COORDINATES?
  JSON.parse(localStorage.weather_app_LEFT_COORDINATES)[0]:
  {lat:59.938732,lon:30.316229}
const rightCoordinate =
  localStorage.weather_app_RIGHT_COORDINATES?
  JSON.parse(localStorage.weather_app_RIGHT_COORDINATES)[0]:
  {lat: 34.7743988, lon: 32.4231586}
const initialState = {
  citiesList: [],
  coordLEFT: lefCoordinate,
  coordRIGHT: rightCoordinate,
};
export const weatherReducer = (state = initialState, action) => {
  switch (action.type) {
    case FETCH_CITIES_LIST:
      return { ...state, citiesList: action.payload };
    case FETCH_LEFT_COORDINATES:
      return { ...state, coordLEFT: action.payload };
    case FETCH_RIGHT_COORDINATES:
      return { ...state, coordRIGHT: action.payload };
    default:
      return state;
  }
};
