import {
  FETCH_CITIES_LIST,
  FETCH_RIGHT_COORDINATES,
  FETCH_LEFT_COORDINATES,
  CHANGE_LANG,
} from "./types";

const apiKey = process.env.REACT_APP_API_KEY;

export function changeLang(lang) {
  localStorage.weather_app_lang = lang;
  return (dispatch) => {
    dispatch({ type: CHANGE_LANG, payload: lang });
  };
}

export function fetchCitiesList(cityName) {
  return async (dispatch) => {
    if (!cityName.length) return;
    try {
      const response = await fetch(
        `https://api.openweathermap.org/geo/1.0/direct?q=${cityName}&limit=5&appid=${apiKey}`
      );
      const json = await response.json();
      dispatch({ type: FETCH_CITIES_LIST, payload: json });
    } catch (e) {
      console.error("action error", e);
    }
  };
}

export function fetchCityCoordinate(input, side) {
  let cityCode = input.toLowerCase().split(" - ")
  cityCode.splice(1,1);
  cityCode = cityCode.join(',')
  return async (dispatch) => {
    if (!cityCode.length) return;
    try {
      const response = await fetch(
        `https://api.openweathermap.org/geo/1.0/direct?q=${cityCode}&limit=5&appid=${apiKey}`
      );
      if (!response.ok) return
      const json = await response.json();
      if (!json.length) return
      if (side === "LEFT") {
        localStorage.weather_app_LEFT_COORDINATES = JSON.stringify(json);
        dispatch({ type: FETCH_LEFT_COORDINATES, payload: json[0] });
      }
      if (side === "RIGHT"){
        localStorage.weather_app_RIGHT_COORDINATES = JSON.stringify(json);
        dispatch({ type: FETCH_RIGHT_COORDINATES, payload: json[0] });
      }
    } catch (e) {
      console.error("action error", e);
    }
  };
}
